<template>
	<div>
		<div>
			<van-nav-bar title="发布" />
		</div>
		<div class="from">
			<van-form>
				<van-field v-model="title" name="title" placeholder="请输入标题"
					:rules="[{ required: true, message: '请输入标题' }]" />
				<van-field v-model="content" type="text" name="content" placeholder="请输入内容"
					:rules="[{ required: true, message: '请输入内容' }]" />
			</van-form>
			<!-- 上传图片 -->
			<van-field name="uploader" style="margin-top: 10px;">
				<template #input>
					<van-uploader :after-read="afterRead" />
				</template>
			</van-field>
			<div style="margin: 16px;">
				<van-button round block type="info" native-type="submit" color="#07c160" @click="onSubmit">提交
				</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Field,
		Form,
		Uploader
	} from 'vant';
	export default {
		data() {
			return {
				name: 'publish',
				title: '',
				text: '',
				content: '',
				// uploader: [{
				// 	// url: 'https://img01.yzcdn.cn/vant/leaf.jpg'
				// }],
				uid: '',
				username: '',

			}
		},
		components: {
			[Field.name]: Field,
			[Form.name]: Form,
			[Uploader.name]: Uploader
		},
		methods: {
			onSubmit() {
				// console.log(this.username);
				// return
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article', {
					title: this.title,
					content: this.content,
					cate_id: "610128169b33ed00018ed5df",
					cate_name: "今日头条",
					author: this.username,
					author_id: this.uid,
					imageSrc: []
				}).then(res => {
					console.log(res.data);
				})
			},
			afterRead(file) {
				// 此时可以自行将文件上传至服务器
				console.log(file);
			},
		},
		mounted() {
			// 获取id
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			console.log(userInfo);
			this.uid = userInfo.uid
			this.username = userInfo.username
		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}
</style>
