<template>
	<div>
		<div class="pinfo">
			<ul class="tableList">
				<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
					<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
						<router-link to="/details">
							<li v-for="item in centerlist" :key="item.id" style="color: #000;">
								<div class="top">
									<p>{{item.title}}</p>
									<img width="110" height="100" :src="item.imageSrc[0]" v-show="item.imageSrc.length"
										style="margin-top: 10px;">
								</div>
								<div class="bottom">
									<span>{{item.author}} </span>
									<div class="bottom-right">
										<span>{{item.comment}}评论</span>
										<span> 发布于{{new Date(item.time).toLocaleDateString()}}</span>
									</div>
								</div>
							</li>
						</router-link>
					</van-list>
				</van-pull-refresh>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import {
		Image as VanImage,
		List,
	} from 'vant';
	export default {
		components: {
			[VanImage.name]: VanImage,
			[List.name]: List,
		},
		data() {
			return {
				centerlist: [],
				list: [],
				loading: false,
				finished: false,
				refreshing: false,
				skip: 0,
				limit: 10
			}
		},
		methods: {
			onLoad(creatId) {
				setTimeout(() => {
					axios({
						method: 'post',
						url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list",
						data: {
							"cate_id": creatId ? creatId : "610128169b33ed00018ed5df",
							"skip": this.skip,
							"limit": this.limit
						}
					}).then((data) => {
						console.log(data.data);
						this.centerlist = data.data.data
						console.log(this.centerlist);
						this.refreshing = false;
						this.loading = false;
						this.skip++;
						this.limit += 10;
						if (this.limit > 40) {
							this.finished = true;
						}
					})
				}, 1000);
			},
			onRefresh() {
				// 清空列表数据
				this.finished = false;
				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.onLoad();
			},
			getData(creatId) {
				axios({
					method: 'post',
					url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list",
					data: {
						"cate_id": creatId ? creatId : "610128169b33ed00018ed5df",
						"skip": 0,
						"limit": 10
					}
				}).then((data) => {
					// console.log(data.data.data);
					this.centerlist = data.data.data
				})
			},
		},
		mounted() {
			this.getData();
			this.$bus.$on('creatId', this.getData)
		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-pull-refresh {
		margin-top: 20px;
	}

	/deep/ .van-pull-refresh__track {
		position: static;
	}

	.van-list {
		margin-bottom: 50px;
	}

	.doge {
		width: 140px;
		height: 72px;
		margin-top: 8px;
		border-radius: 4px;
	}

	.pinfo {
		margin-top: 85px;
	}

	.tableList {
		height: 100%;
	}

	.tableList li {
		display: flex;
		width: 90%;
		height: 200px;
		margin: 0 auto;
		flex-direction: column;
		align-items: flex-start;
		border-bottom: 1px solid #ccc;
		justify-content: space-between;
		padding-left: 10px;
		padding-bottom: 5px;
		padding-top: 5px;
	}

	.top {
		display: flex;
	}

	.top p {
		width: 60vw;
		text-align: left;
		margin-top: 10px;
	}

	.bottom {
		width: 100%;
		text-align: left;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
</style>
