<template>
	<div id="app">
		<router-view />
		<van-tabbar v-model="active" v-if="mainPage" active-color="#ee0a24" route>
			<van-tabbar-item replace to="/" icon="home-o">首页</van-tabbar-item>
			<van-tabbar-item replace v-model="key2" :to="key2" icon="add-o">发布</van-tabbar-item>
			<van-tabbar-item replace v-model="key" :to="key" icon="manager-o">我的</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>
	import home from '@/pages/home.vue';
	export default {
		data() {
			return {
				active: 0,
				value: 1,
				key: '/login',
				key2: '/login'
			}
		},
		components: {
			home,
		},
		computed: {
			mainPage() {
				return this.$route.meta.root
			}
		},
		created() {
			// 判断登录状态
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));

			console.log(userInfo);
			if (userInfo.isLogin = false) {
				this.key = '/login'
				this.key2 = '/login'
			}
			// console.log('userInfo', userInfo.isLogin);
			if (userInfo.isLogin) {
				this.key = '/my'
				this.key2 = '/publish'
			}
		},
		mounted() {

		}
	};
</script>

<style lang="scss">
	* {
		margin: 0;
		padding: 0;
	}

	#app {
		font-family: Avenir, Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
	}

	.box {
		width: 500px;
	}
</style>
