<template>
	<div class="app">
		<!-- 头部 -->
		<div class="header">
			<div class="top" v-if="isshow">
				<div class="top-left">
					<p class="avatar" style="">
						<van-icon name="warning-o" />
					</p>
					<!-- 名称 -->
					<p style="margin-left: 20px;">{{uname}}</p>
				</div>
				<!-- 编辑资料 -->
				<div class="top-right">
					<router-link to="/user">
						<button>
							编辑资料
						</button>
					</router-link>
				</div>
			</div>
			<div class="tops" style="padding-bottom: 20px;" v-else>
				<p style="margin-bottom: 20px;padding-top: 40px;">登录显示更多精彩</p>
				<van-button color="#b91114" round to="/login">登录后更懂你</van-button>
			</div>
			<div class="top-bottom">
				<div class="bottom-left">
					<p>1</p>
					<p>头条</p>
				</div>
				<div class="bottom-right">
					<p>3</p>
					<p>获赞</p>
				</div>
			</div>
		</div>
		<!-- 列表 -->
		<div class="nav">
			<van-grid clickable :column-num="2">
				<van-grid-item icon="star-o" text="收藏" to="" />
				<van-grid-item icon="clock-o" text="历史" />
			</van-grid>
		</div>
		<div class="list">
			<!-- 修改密码 -->
			<van-cell is-link @click="showPopup">修改密码</van-cell>
			<van-popup v-model="show">
				<van-form @submit="onSubmit">
					<van-field v-model="oldPassword" type="password" name="oldPassword" placeholder="请填写旧密码"
						:rules="[{ required: true, message: '请填写旧密码' }]" />
					<van-field v-model="newPassword" type="password" name="newPassword" placeholder="请填写新密码"
						:rules="[{ required: true, message: '请填写新密码' }]" />
					<div style="margin: 16px;">
						<van-button round block type="info" native-type="submit" style="color: #fff;"">提交</van-button>
					</div>
				</van-form>
			</van-popup>
			<van-cell is-link>联系我们</van-cell>
			<van-cell is-link>关于我们</van-cell>
		</div>
		<!-- 退出登录 -->
		<div class=" quit">
							<van-button type="primary" block color="#fff" @click="logout" v-show="isshow">退出登录
							</van-button>
					</div>
		</div>
</template>

<script>
	import axios from 'axios';
	import {
		Popup,
		CellGroup,
		Grid,
		GridItem,
		Form,
		Field,
		Toast
	} from 'vant';
	export default {
		components: {
			[Popup.name]: Popup,
			[CellGroup.name]: CellGroup,
			[Grid.name]: Grid,
			[GridItem.name]: GridItem,
			[Form.name]: Form,
			[Field.name]: Field,
		},
		data() {
			return {
				name: 'my',
				show: false,
				username: "",
				password: '',
				isshow: false,
				uname: '',
				token: '',
				oldPassword: '',
				newPassword: '',
				uid: ''
			}
		},
		methods: {
			// 修改密码
			showPopup() {
				this.show = true;
			},
			// 提交修改密码请求
			onSubmit() {
				console.log('提交');
				if (this.oldPassword === '' || this.newPassword === '') {
					Toast('请输入密码')
				} else {
					axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/updatePwd', {
						oldPassword: this.oldPassword,
						newPassword: this.newPassword,
						uid: this.uid
					}).then(res => {
						console.log(res.data);
						Toast(res.data.msg)
						localStorage.clear();
						setTimeout(() => {
							this.$router.push({
								path: '/'
							}, () => {}, () => {}); //退出登录后1秒后跳转至首页
							Toast('请重新登录')
						}, 1000)
					})
				}
			},
			//退出登录
			logout() {
				// this.$store.state.userInfo = null
				localStorage.clear();
				console.log(window.localStorage);
				let userInfo = {
					isLogin: false
				};
				window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
				setTimeout(() => {
					this.$router.push({
						path: '/'
					}, () => {}, () => {}); //退出登录后1秒后跳转至首页
				}, 1000)
			},
		},
		created() {
			// 检测登录状态
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			// console.log('userInfo', userInfo);
			if (userInfo.isLogin) {
				this.isshow = false
			};
			if (userInfo.isLogin) {
				this.isshow = true
				// 获取信息
				this.uname = userInfo.nickname;
				this.token = userInfo.token;
				this.uid = userInfo.uid;
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/getuserInfo', {
						token: this.token
					})
					.then(res => {
						console.log(res.data.msg);
					})
			}
		}
	}
</script>

<style lang="less" scoped>
	.app {
		width: 100%;
		height: 100vh;
		background-color: #eeeeee;
	}

	.header {
		background-color: #07c160;
	}

	.header p {
		color: #fff;
	}

	.avatar {
		width: 50px;
		height: 50px;
		border-radius: 25px;
		background-color: #fff;
		position: relative;
	}

	.avatar>.van-icon {
		position: absolute;
		top: 35%;
		left: 35%;
	}

	.top {
		padding-top: 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 20px;
	}

	.top-left {
		display: flex;
		align-items: center;
		margin-left: 20px;
	}

	.top-right button {
		width: 70px;
		// height: 30px;
		font-size: 12px;
		border-radius: 10px;
		background-color: #fff;
		color: #000;
		border: none;
		margin-right: 20px;
	}

	.top-bottom {
		display: flex;
		justify-content: space-around;
		padding-bottom: 10px;
	}

	/deep/ .van-button__text {
		color: red;
	}

	.tops .van-button__text {
		color: #fff;
	}

	.nav {
		margin-bottom: 5px;
	}

	.list {
		margin-bottom: 5px;
	}

	/deep/.van-icon {
		color: #ee0a24;
	}
</style>
