<template>
	<div>
		<!-- 标题 -->
		<div>
			<van-nav-bar title="注册 / 登录" left-arrow @click-left="onClickLeft" />
		</div>
		<!-- 登录 -->
		<van-form @submit="onSubmit" v-if="key">
			<div class="phone">
				<van-icon name="phone-o" />
				<van-field v-model="username" name="username" placeholder="请输入手机号"
					:rules="[{ required: true, message: '请输入手机号' }]" />
			</div>
			<div class="password">
				<van-icon name="eye-o" />
				<van-field v-model="password" type="password" name="password" placeholder="请输入密码"
					:rules="[{ required: true, message: '请输入密码' }]" />
			</div>
			<div class="text">
				<p>还没有注册吗？去 <a @click="change">注册</a></p>
			</div>
			<div style="margin: 16px;">
				<van-button round block type="info" native-type="submit" color="#07c160">登录
				</van-button>
			</div>
		</van-form>
		<!--  注册 -->
		<van-form v-else>
			<div class="phone">
				<van-icon name="phone-o" />
				<van-field v-model="username" name="username" placeholder="请输入手机号"
					:rules="[{ required: true, message: '请输入手机号' }]" />
			</div>
			<div class="password">
				<van-icon name="eye-o" />
				<van-field v-model="password" type="password" name="password" placeholder="请输入密码"
					:rules="[{ required: true, message: '请输入密码' }]" />
			</div>
			<div class="primary">
				<van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
					<template #button>
						<van-button size="small" type="primary" @click='onprimary'>发送验证码</van-button>
					</template>
				</van-field>
			</div>
			<div class="text">
				<p>已经注册了？去 <a @click="change">登录</a></p>
			</div>
			<div style="margin: 16px;">
				<van-button round block type="info" native-type="submit" color="#07c160" @click='enroll'>注册
				</van-button>
			</div>
		</van-form>
	</div>
</template>

<script>
	import axios from 'axios';
	import store from '@/store/index'
	import {
		Form,
		Field,
		Toast
	} from 'vant';
	export default {
		components: {
			store,
			[Form.name]: Form,
			[Field.name]: Field,
			[Toast.name]: Toast,
		},
		data() {
			return {
				username: '',
				password: '',
				sms: '',
				key: true,
				name: 'Login'
			};
		},
		methods: {
			// 返回上一级路由
			onClickLeft() {
				this.$router.push('/my')
			},
			// 注册登录切换
			change() {
				this.key = !this.key
			},
			// 提交登录
			onSubmit() {
				// console.log(this.username);
				// console.log(this.password);
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/login',
					data: {
						username: this.username,
						password: this.password
					}
				}).then(res => {
					console.log(res.data);
					console.log(res.data.msg);
					if (res.data.msg == "登录成功") {
						let userInfo = {
							isLogin: true,
							uid: res.data.uid,
							birthday: res.data.userInfo.birthday,
							avatar: res.data.userInfo.avatar,
							sex: res.data.userInfo.sex,
							id: res.data.userInfo._id,
							nickname: res.data.userInfo.nickname,
							token: res.data.token,
							username: res.data.username
						};
						window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
						// this.$store.state.userInfo = userInfo
						// console.log('this.$store.state.userInfo', this.$store.state.userInfo)
						this.$router.push('/my')
					}
				}, err => {
					console.log(err);
				})
			},
			// 获取验证码
			onprimary() {
				if (this.username == '') {
					Toast('手机号不能为空');
				} else {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
						data: {
							mobile: this.username,
							type: 'register'
						}
					}).then(res => {
						console.log(res.data);
						this.sms = res.data.data
					})
				}
			},
			// 注册
			enroll() {
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/reg',
					data: {
						username: this.username,
						password: this.password,
						vercode: this.sms
					}
				}).then(res => {
					console.log(res.data);
					// this.sms = res.data.data
				}).catch(err => {
					console.log(err.data);
				})
			}
		},
		mounted() {

		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/.van-nav-bar .van-icon {
		color: #fff;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	.phone,
	.password {
		display: flex;
	}


	.van-from {
		// position: relative;
	}

	.van-icon {
		// position: absolute;
		// left: 10px;
		// top: 50px;
		// // display: block;
		// width: 50px;
		// height: 50px;
		width: 10%;
		// text-align: center;
		margin-top: 13px;
	}

	.text {
		text-align: left;
		margin-left: 10px;
		font-size: 12px;
	}

	.text a {
		color: #c1080b;
	}
</style>
